'use client'

import { useState, useEffect, useCallback } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Progress } from '@/components/ui/progress'
import { Badge } from '@/components/ui/badge'
import { 
  Play, 
  Pause, 
  Square, 
  CheckCircle, 
  XCircle, 
  AlertTriangle,
  Database,
  Search,
  Package,
  TrendingUp,
  Trash2,
  Clock,
  BarChart3
} from 'lucide-react'

// ÌÖåÏä§Ìä∏ Í≤∞Í≥º Ïù∏ÌÑ∞ÌéòÏù¥Ïä§
interface TestResult {
  testName: string
  status: 'pending' | 'running' | 'passed' | 'failed' | 'error'
  startTime?: number
  endTime?: number
  duration?: number
  totalTests: number
  passedTests: number
  failedTests: number
  errorTests: number
  details: string[]
}

// Ïû¨Í≥† ÌÖåÏä§Ìä∏ Îç∞Ïù¥ÌÑ∞ Ïù∏ÌÑ∞ÌéòÏù¥Ïä§
interface StockTestData {
  id: string
  name: string
  specification: string
  unit_price: number
  current_quantity: number
  total_amount: number
  category: string
  stock_status: 'normal' | 'low_stock'
}

export default function StockTestPanel() {
  const [isRunning, setIsRunning] = useState(false)
  const [currentTest, setCurrentTest] = useState<string>('')
  const [progress, setProgress] = useState(0)
  const [testResults, setTestResults] = useState<TestResult[]>([])
  const [overallStats, setOverallStats] = useState({
    totalTests: 0,
    passedTests: 0,
    failedTests: 0,
    errorTests: 0,
    startTime: 0,
    endTime: 0
  })

  // ÌÖåÏä§Ìä∏ Ï¥àÍ∏∞Ìôî
  const initializeTests = useCallback(() => {
    const tests: TestResult[] = [
      {
        testName: 'ÏûÖÍ≥† Í∏∞Îä• ÌÖåÏä§Ìä∏',
        status: 'pending',
        totalTests: 1000000,
        passedTests: 0,
        failedTests: 0,
        errorTests: 0,
        details: []
      },
      {
        testName: 'Ï∂úÍ≥† Í∏∞Îä• ÌÖåÏä§Ìä∏',
        status: 'pending',
        totalTests: 1000000,
        passedTests: 0,
        failedTests: 0,
        errorTests: 0,
        details: []
      },
      {
        testName: 'ÌèêÍ∏∞ Í∏∞Îä• ÌÖåÏä§Ìä∏',
        status: 'pending',
        totalTests: 1000000,
        passedTests: 0,
        failedTests: 0,
        errorTests: 0,
        details: []
      },
      {
        testName: 'Í≤ÄÏÉâ Í∏∞Îä• ÌÖåÏä§Ìä∏',
        status: 'pending',
        totalTests: 1000000,
        passedTests: 0,
        failedTests: 0,
        errorTests: 0,
        details: []
      },
      {
        testName: 'Ïû¨Í≥† Í≥ÑÏÇ∞ ÌÖåÏä§Ìä∏',
        status: 'pending',
        totalTests: 1000000,
        passedTests: 0,
        failedTests: 0,
        errorTests: 0,
        details: []
      },
      {
        testName: 'Îç∞Ïù¥ÌÑ∞ Î¨¥Í≤∞ÏÑ± ÌÖåÏä§Ìä∏',
        status: 'pending',
        totalTests: 1000000,
        passedTests: 0,
        failedTests: 0,
        errorTests: 0,
        details: []
      }
    ]
    
    setTestResults(tests)
    setOverallStats({
      totalTests: tests.length * 1000000,
      passedTests: 0,
      failedTests: 0,
      errorTests: 0,
      startTime: 0,
      endTime: 0
    })
  }, [])

  // ÌÖåÏä§Ìä∏ ÏãúÏûë
  const startTests = useCallback(async () => {
    setIsRunning(true)
    setOverallStats((prev: any) => ({ ...prev, startTime: Date.now() }))
    
    // Í∞Å ÌÖåÏä§Ìä∏Î•º ÏàúÏ∞®Ï†ÅÏúºÎ°ú Ïã§Ìñâ
    for (let i = 0; i < testResults.length; i++) {
      if (!isRunning) break
      
      setCurrentTest(testResults[i].testName)
      await runTest(i)
      setProgress(((i + 1) / testResults.length) * 100)
    }
    
    setIsRunning(false)
    setCurrentTest('')
    setOverallStats((prev: any) => ({ ...prev, endTime: Date.now() }))
  }, [testResults, isRunning])

  // ÌÖåÏä§Ìä∏ Ï§ëÏßÄ
  const stopTests = useCallback(() => {
    setIsRunning(false)
    setCurrentTest('')
  }, [])

  // ÌÖåÏä§Ìä∏ ÏùºÏãúÏ†ïÏßÄ
  const pauseTests = useCallback(() => {
    setIsRunning(false)
  }, [])

  // ÌÖåÏä§Ìä∏ Ïû¨ÏÑ§Ï†ï
  const resetTests = useCallback(() => {
    setIsRunning(false)
    setCurrentTest('')
    setProgress(0)
    initializeTests()
  }, [initializeTests])

  // Í∞úÎ≥Ñ ÌÖåÏä§Ìä∏ Ïã§Ìñâ
  const runTest = useCallback(async (testIndex: number) => {
    const test = testResults[testIndex]
    test.status = 'running'
    test.startTime = Date.now()
    
    setTestResults([...testResults])
    
    try {
      switch (testIndex) {
        case 0:
          await runStockInTest(test)
          break
        case 1:
          await runStockOutTest(test)
          break
        case 2:
          await runDisposalTest(test)
          break
        case 3:
          await runSearchTest(test)
          break
        case 4:
          await runCalculationTest(test)
          break
        case 5:
          await runIntegrityTest(test)
          break
      }
      
      test.status = 'passed'
      test.endTime = Date.now()
      test.duration = test.endTime - test.startTime
      
    } catch (error) {
      test.status = 'error'
      test.endTime = Date.now()
      test.duration = test.endTime - test.startTime
      test.details.push(`Ïò§Î•ò: ${error instanceof Error ? error.message : 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò'}`)
    }
    
    setTestResults([...testResults])
    updateOverallStats()
  }, [testResults])

  // ÏûÖÍ≥† Í∏∞Îä• ÌÖåÏä§Ìä∏
  const runStockInTest = useCallback(async (test: TestResult) => {
    const batchSize = 10000 // Ìïú Î≤àÏóê Ï≤òÎ¶¨Ìï† ÌÖåÏä§Ìä∏ Ïàò
    
    for (let i = 0; i < test.totalTests; i += batchSize) {
      if (!isRunning) break
      
      const currentBatch = Math.min(batchSize, test.totalTests - i)
      
      // Î∞∞Ïπò Îã®ÏúÑÎ°ú ÌÖåÏä§Ìä∏ Ïã§Ìñâ
      for (let j = 0; j < currentBatch; j++) {
        const testData = generateStockInTestData(i + j)
        
        try {
          // ÏûÖÍ≥† Î°úÏßÅ ÏãúÎÆ¨Î†àÏù¥ÏÖò
          const result = simulateStockIn(testData)
          
          if (result.success) {
            test.passedTests++
          } else {
            test.failedTests++
            test.details.push(`ÌÖåÏä§Ìä∏ ${i + j + 1}: ${result.error}`)
          }
          
        } catch (error) {
          test.errorTests++
          test.details.push(`ÌÖåÏä§Ìä∏ ${i + j + 1}: ${error instanceof Error ? error.message : 'Ïò§Î•ò'}`)
        }
      }
      
      // ÏßÑÌñâÎ•† ÏóÖÎç∞Ïù¥Ìä∏
      const progress = ((i + currentBatch) / test.totalTests) * 100
      setProgress(progress)
      
      // UI ÏóÖÎç∞Ïù¥Ìä∏Î•º ÏúÑÌïú ÏßÄÏó∞
      await new Promise(resolve => setTimeout(resolve, 1))
    }
  }, [isRunning])

  // Ï∂úÍ≥† Í∏∞Îä• ÌÖåÏä§Ìä∏
  const runStockOutTest = useCallback(async (test: TestResult) => {
    const batchSize = 10000
    
    for (let i = 0; i < test.totalTests; i += batchSize) {
      if (!isRunning) break
      
      const currentBatch = Math.min(batchSize, test.totalTests - i)
      
      for (let j = 0; j < currentBatch; j++) {
        const testData = generateStockOutTestData(i + j)
        
        try {
          const result = simulateStockOut(testData)
          
          if (result.success) {
            test.passedTests++
          } else {
            test.failedTests++
            test.details.push(`ÌÖåÏä§Ìä∏ ${i + j + 1}: ${result.error}`)
          }
          
        } catch (error) {
          test.errorTests++
          test.details.push(`ÌÖåÏä§Ìä∏ ${i + j + 1}: ${error instanceof Error ? error.message : 'Ïò§Î•ò'}`)
        }
      }
      
      const progress = ((i + currentBatch) / test.totalTests) * 100
      setProgress(progress)
      
      await new Promise(resolve => setTimeout(resolve, 1))
    }
  }, [isRunning])

  // ÌèêÍ∏∞ Í∏∞Îä• ÌÖåÏä§Ìä∏
  const runDisposalTest = useCallback(async (test: TestResult) => {
    const batchSize = 10000
    
    for (let i = 0; i < test.totalTests; i += batchSize) {
      if (!isRunning) break
      
      const currentBatch = Math.min(batchSize, test.totalTests - i)
      
      for (let j = 0; j < currentBatch; j++) {
        const testData = generateDisposalTestData(i + j)
        
        try {
          const result = simulateDisposal(testData)
          
          if (result.success) {
            test.passedTests++
          } else {
            test.failedTests++
            test.details.push(`ÌÖåÏä§Ìä∏ ${i + j + 1}: ${result.error}`)
          }
          
        } catch (error) {
          test.errorTests++
          test.details.push(`ÌÖåÏä§Ìä∏ ${i + j + 1}: ${error instanceof Error ? error.message : 'Ïò§Î•ò'}`)
        }
      }
      
      const progress = ((i + currentBatch) / test.totalTests) * 100
      setProgress(progress)
      
      await new Promise(resolve => setTimeout(resolve, 1))
    }
  }, [isRunning])

  // Í≤ÄÏÉâ Í∏∞Îä• ÌÖåÏä§Ìä∏
  const runSearchTest = useCallback(async (test: TestResult) => {
    const batchSize = 10000
    
    for (let i = 0; i < test.totalTests; i += batchSize) {
      if (!isRunning) break
      
      const currentBatch = Math.min(batchSize, test.totalTests - i)
      
      for (let j = 0; j < currentBatch; j++) {
        const testData = generateSearchTestData(i + j)
        
        try {
          const result = simulateSearch(testData)
          
          if (result.success) {
            test.passedTests++
          } else {
            test.failedTests++
            test.details.push(`ÌÖåÏä§Ìä∏ ${i + j + 1}: ${result.error}`)
          }
          
        } catch (error) {
          test.errorTests++
          test.details.push(`ÌÖåÏä§Ìä∏ ${i + j + 1}: ${error instanceof Error ? error.message : 'Ïò§Î•ò'}`)
        }
      }
      
      const progress = ((i + currentBatch) / test.totalTests) * 100
      setProgress(progress)
      
      await new Promise(resolve => setTimeout(resolve, 1))
    }
  }, [isRunning])

  // Ïû¨Í≥† Í≥ÑÏÇ∞ ÌÖåÏä§Ìä∏
  const runCalculationTest = useCallback(async (test: TestResult) => {
    const batchSize = 10000
    
    for (let i = 0; i < test.totalTests; i += batchSize) {
      if (!isRunning) break
      
      const currentBatch = Math.min(batchSize, test.totalTests - i)
      
      for (let j = 0; j < currentBatch; j++) {
        const testData = generateCalculationTestData(i + j)
        
        try {
          const result = simulateCalculation(testData)
          
          if (result.success) {
            test.passedTests++
          } else {
            test.failedTests++
            test.details.push(`ÌÖåÏä§Ìä∏ ${i + j + 1}: ${result.error}`)
          }
          
        } catch (error) {
          test.errorTests++
          test.details.push(`ÌÖåÏä§Ìä∏ ${i + j + 1}: ${error instanceof Error ? error.message : 'Ïò§Î•ò'}`)
        }
      }
      
      const progress = ((i + currentBatch) / test.totalTests) * 100
      setProgress(progress)
      
      await new Promise(resolve => setTimeout(resolve, 1))
    }
  }, [isRunning])

  // Îç∞Ïù¥ÌÑ∞ Î¨¥Í≤∞ÏÑ± ÌÖåÏä§Ìä∏
  const runIntegrityTest = useCallback(async (test: TestResult) => {
    const batchSize = 10000
    
    for (let i = 0; i < test.totalTests; i += batchSize) {
      if (!isRunning) break
      
      const currentBatch = Math.min(batchSize, test.totalTests - i)
      
      for (let j = 0; j < currentBatch; j++) {
        const testData = generateIntegrityTestData(i + j)
        
        try {
          const result = simulateIntegrityCheck(testData)
          
          if (result.success) {
            test.passedTests++
          } else {
            test.failedTests++
            test.details.push(`ÌÖåÏä§Ìä∏ ${i + j + 1}: ${result.error}`)
          }
          
        } catch (error) {
          test.errorTests++
          test.details.push(`ÌÖåÏä§Ìä∏ ${i + j + 1}: ${error instanceof Error ? error.message : 'Ïò§Î•ò'}`)
        }
      }
      
      const progress = ((i + currentBatch) / test.totalTests) * 100
      setProgress(progress)
      
      await new Promise(resolve => setTimeout(resolve, 1))
    }
  }, [isRunning])

  // ÌÖåÏä§Ìä∏ Îç∞Ïù¥ÌÑ∞ ÏÉùÏÑ± Ìï®ÏàòÎì§
  const generateStockInTestData = (index: number) => ({
    itemName: `ÌÖåÏä§Ìä∏ÌíàÎ™©_${index}`,
    quantity: Math.floor(Math.random() * 1000) + 1,
    unitPrice: Math.floor(Math.random() * 100000) + 1000,
    notes: `ÌÖåÏä§Ìä∏ ÏûÖÍ≥† ${index}`,
    conditionType: ['new', 'used_good', 'used_defective', 'unknown'][Math.floor(Math.random() * 4)] as any,
    reason: `ÌÖåÏä§Ìä∏ ÏÇ¨Ïú† ${index}`,
    orderedBy: `Ï£ºÎ¨∏Ïûê_${index}`,
    receivedBy: `ÏûÖÍ≥†Ïûê_${index}`
  })

  const generateStockOutTestData = (index: number) => ({
    itemId: `item_${index}`,
    quantity: Math.floor(Math.random() * 100) + 1,
    project: `ÌîÑÎ°úÏ†ùÌä∏_${index}`,
    notes: `ÌÖåÏä§Ìä∏ Ï∂úÍ≥† ${index}`,
    isRental: Math.random() > 0.5,
    returnDate: Math.random() > 0.5 ? new Date(Date.now() + Math.random() * 30 * 24 * 60 * 60 * 1000).toISOString() : undefined,
    issuedBy: `Ï∂úÍ≥†Ïûê_${index}`
  })

  const generateDisposalTestData = (index: number) => ({
    stockInId: `stock_in_${index}`,
    itemId: `item_${index}`,
    quantity: Math.floor(Math.random() * 50) + 1,
    disposedBy: `ÌèêÍ∏∞Ïûê_${index}`,
    disposedAt: new Date().toISOString(),
    reason: `ÌÖåÏä§Ìä∏ ÌèêÍ∏∞ ${index}`,
    notes: `ÌèêÍ∏∞ ÎπÑÍ≥† ${index}`
  })

  const generateSearchTestData = (index: number) => ({
    query: `Í≤ÄÏÉâÏñ¥_${index}`,
    category: ['Ï†ÑÍ∏∞ÏûêÏû¨', 'Í≥µÍµ¨', 'ÏÜåÎ™®Ìíà', 'ÏïàÏ†ÑÏö©Ìíà'][Math.floor(Math.random() * 4)],
    minPrice: Math.floor(Math.random() * 10000),
    maxPrice: Math.floor(Math.random() * 100000) + 10000,
    inStock: Math.random() > 0.5
  })

  const generateCalculationTestData = (index: number) => ({
    currentQuantity: Math.floor(Math.random() * 1000),
    unitPrice: Math.floor(Math.random() * 100000) + 1000,
    stockInQuantity: Math.floor(Math.random() * 500),
    stockOutQuantity: Math.floor(Math.random() * 300),
    adjustmentQuantity: Math.floor(Math.random() * 100) - 50
  })

  const generateIntegrityTestData = (index: number) => ({
    itemId: `item_${index}`,
    name: `ÌíàÎ™©_${index}`,
    specification: `Í∑úÍ≤©_${index}`,
    unitPrice: Math.floor(Math.random() * 100000) + 1000,
    currentQuantity: Math.floor(Math.random() * 1000),
    totalAmount: 0,
    category: `Ïπ¥ÌÖåÍ≥†Î¶¨_${index}`,
    stockStatus: ['normal', 'low_stock'][Math.floor(Math.random() * 2)] as any
  })

  // ÏãúÎÆ¨Î†àÏù¥ÏÖò Ìï®ÏàòÎì§
  const simulateStockIn = (data: any) => {
    try {
      // ÏûÖÎ†•Í∞í Í≤ÄÏ¶ù
      if (!data.itemName || data.itemName.length < 1) {
        return { success: false, error: 'ÌíàÎ™©Î™ÖÏù¥ ÎπÑÏñ¥ÏûàÏäµÎãàÎã§' }
      }
      
      if (data.quantity <= 0 || data.quantity > 999999) {
        return { success: false, error: 'ÏàòÎüâÏù¥ Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§' }
      }
      
      if (data.unitPrice < 0 || data.unitPrice > 999999999) {
        return { success: false, error: 'Îã®Í∞ÄÍ∞Ä Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§' }
      }
      
      // Ïû¨Í≥† Í≥ÑÏÇ∞ ÏãúÎÆ¨Î†àÏù¥ÏÖò
      const totalAmount = data.quantity * data.unitPrice
      
      if (totalAmount > 999999999999) {
        return { success: false, error: 'Ï¥ù Í∏àÏï°Ïù¥ ÎÑàÎ¨¥ ÌÅΩÎãàÎã§' }
      }
      
      return { success: true, totalAmount }
      
    } catch (error) {
      return { success: false, error: 'ÏûÖÍ≥† Ï≤òÎ¶¨ Ï§ë Ïò§Î•ò Î∞úÏÉù' }
    }
  }

  const simulateStockOut = (data: any) => {
    try {
      if (!data.itemId) {
        return { success: false, error: 'ÌíàÎ™© IDÍ∞Ä ÏóÜÏäµÎãàÎã§' }
      }
      
      if (data.quantity <= 0 || data.quantity > 999999) {
        return { success: false, error: 'ÏàòÎüâÏù¥ Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§' }
      }
      
      // Ïû¨Í≥† Î∂ÄÏ°± Ï≤¥ÌÅ¨ ÏãúÎÆ¨Î†àÏù¥ÏÖò
      const currentStock = Math.floor(Math.random() * 1000) + 100
      
      if (data.quantity > currentStock) {
        return { success: false, error: 'Ïû¨Í≥†Í∞Ä Î∂ÄÏ°±Ìï©ÎãàÎã§' }
      }
      
      return { success: true, remainingStock: currentStock - data.quantity }
      
    } catch (error) {
      return { success: false, error: 'Ï∂úÍ≥† Ï≤òÎ¶¨ Ï§ë Ïò§Î•ò Î∞úÏÉù' }
    }
  }

  const simulateDisposal = (data: any) => {
    try {
      if (!data.itemId) {
        return { success: false, error: 'ÌíàÎ™© IDÍ∞Ä ÏóÜÏäµÎãàÎã§' }
      }
      
      if (data.quantity <= 0) {
        return { success: false, error: 'ÌèêÍ∏∞ ÏàòÎüâÏù¥ Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§' }
      }
      
      if (!data.reason || data.reason.length < 1) {
        return { success: false, error: 'ÌèêÍ∏∞ ÏÇ¨Ïú†Í∞Ä ÌïÑÏöîÌï©ÎãàÎã§' }
      }
      
      return { success: true, disposedAt: data.disposedAt }
      
    } catch (error) {
      return { success: false, error: 'ÌèêÍ∏∞ Ï≤òÎ¶¨ Ï§ë Ïò§Î•ò Î∞úÏÉù' }
    }
  }

  const simulateSearch = (data: any) => {
    try {
      if (!data.query || data.query.length < 1) {
        return { success: false, error: 'Í≤ÄÏÉâÏñ¥Í∞Ä ÎπÑÏñ¥ÏûàÏäµÎãàÎã§' }
      }
      
      if (data.query.length > 200) {
        return { success: false, error: 'Í≤ÄÏÉâÏñ¥Í∞Ä ÎÑàÎ¨¥ ÍπÅÎãàÎã§' }
      }
      
      // Í≤ÄÏÉâ Í≤∞Í≥º ÏãúÎÆ¨Î†àÏù¥ÏÖò
      const resultCount = Math.floor(Math.random() * 100)
      
      return { success: true, resultCount }
      
    } catch (error) {
      return { success: false, error: 'Í≤ÄÏÉâ Ï≤òÎ¶¨ Ï§ë Ïò§Î•ò Î∞úÏÉù' }
    }
  }

  const simulateCalculation = (data: any) => {
    try {
      // Ïû¨Í≥† Í≥ÑÏÇ∞ ÏãúÎÆ¨Î†àÏù¥ÏÖò
      const finalQuantity = data.currentQuantity + data.stockInQuantity - data.stockOutQuantity + data.adjustmentQuantity
      const totalAmount = finalQuantity * data.unitPrice
      
      if (finalQuantity < 0) {
        return { success: false, error: 'Ïû¨Í≥†Í∞Ä ÏùåÏàòÍ∞Ä Îê† Ïàò ÏóÜÏäµÎãàÎã§' }
      }
      
      if (totalAmount > 999999999999) {
        return { success: false, error: 'Ï¥ù Í∏àÏï°Ïù¥ ÎÑàÎ¨¥ ÌÅΩÎãàÎã§' }
      }
      
      return { success: true, finalQuantity, totalAmount }
      
    } catch (error) {
      return { success: false, error: 'Í≥ÑÏÇ∞ Ï≤òÎ¶¨ Ï§ë Ïò§Î•ò Î∞úÏÉù' }
    }
  }

  const simulateIntegrityCheck = (data: any) => {
    try {
      // Îç∞Ïù¥ÌÑ∞ Î¨¥Í≤∞ÏÑ± Í≤ÄÏ¶ù
      if (!data.name || data.name.length < 1) {
        return { success: false, error: 'ÌíàÎ™©Î™ÖÏù¥ ÎπÑÏñ¥ÏûàÏäµÎãàÎã§' }
      }
      
      if (data.unitPrice < 0) {
        return { success: false, error: 'Îã®Í∞ÄÍ∞Ä ÏùåÏàòÏùº Ïàò ÏóÜÏäµÎãàÎã§' }
      }
      
      if (data.currentQuantity < 0) {
        return { success: false, error: 'Ïû¨Í≥† ÏàòÎüâÏù¥ ÏùåÏàòÏùº Ïàò ÏóÜÏäµÎãàÎã§' }
      }
      
      // Í≥ÑÏÇ∞Îêú Ï¥ùÏï°Í≥º Ïã§Ï†ú Ï¥ùÏï° ÎπÑÍµê
      const calculatedAmount = data.currentQuantity * data.unitPrice
      if (Math.abs(calculatedAmount - data.totalAmount) > 0.01) {
        return { success: false, error: 'Ï¥ùÏï° Í≥ÑÏÇ∞Ïù¥ ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§' }
      }
      
      return { success: true, calculatedAmount }
      
    } catch (error) {
      return { success: false, error: 'Î¨¥Í≤∞ÏÑ± Í≤ÄÏ¶ù Ï§ë Ïò§Î•ò Î∞úÏÉù' }
    }
  }

  // Ï†ÑÏ≤¥ ÌÜµÍ≥Ñ ÏóÖÎç∞Ïù¥Ìä∏
  const updateOverallStats = useCallback(() => {
    const total = testResults.reduce((sum: number, test: TestResult) => sum + test.totalTests, 0)
    const passed = testResults.reduce((sum: number, test: TestResult) => sum + test.passedTests, 0)
    const failed = testResults.reduce((sum: number, test: TestResult) => sum + test.failedTests, 0)
    const error = testResults.reduce((sum: number, test: TestResult) => sum + test.errorTests, 0)
    
    setOverallStats((prev: any) => ({
      ...prev,
      totalTests: total,
      passedTests: passed,
      failedTests: failed,
      errorTests: error
    }))
  }, [testResults])

  // Ïª¥Ìè¨ÎÑåÌä∏ ÎßàÏö¥Ìä∏ Ïãú ÌÖåÏä§Ìä∏ Ï¥àÍ∏∞Ìôî
  useEffect(() => {
    initializeTests()
  }, [initializeTests])

  // ÌÖåÏä§Ìä∏ ÏÉÅÌÉúÏóê Îî∞Î•∏ ÏïÑÏù¥ÏΩò
  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'pending':
        return <Clock className="h-4 w-4 text-gray-400" />
      case 'running':
        return <Play className="h-4 w-4 text-blue-500 animate-pulse" />
      case 'passed':
        return <CheckCircle className="h-4 w-4 text-green-500" />
      case 'failed':
        return <XCircle className="h-4 w-4 text-red-500" />
      case 'error':
        return <AlertTriangle className="h-4 w-4 text-yellow-500" />
      default:
        return <Clock className="h-4 w-4 text-gray-400" />
    }
  }

  // ÌÖåÏä§Ìä∏ ÏÉÅÌÉúÏóê Îî∞Î•∏ Î∞∞ÏßÄ ÏÉâÏÉÅ
  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'pending':
        return 'bg-gray-100 text-gray-800'
      case 'running':
        return 'bg-blue-100 text-blue-800'
      case 'passed':
        return 'bg-green-100 text-green-800'
      case 'failed':
        return 'bg-red-100 text-red-800'
      case 'error':
        return 'bg-yellow-100 text-yellow-800'
      default:
        return 'bg-gray-100 text-gray-800'
    }
  }

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <div className="max-w-7xl mx-auto space-y-6">
        {/* Ìó§Îçî */}
        <div className="text-center">
          <h1 className="text-3xl font-bold text-gray-900 mb-2">
            üß™ Ïû¨Í≥†Í¥ÄÎ¶¨ ÏãúÏä§ÌÖú ÌÖåÏä§Ìä∏ Ìå®ÎÑê
          </h1>
          <p className="text-lg text-gray-600">
            100ÎßåÎ≤à ÎåÄÍ∑úÎ™® ÌÖåÏä§Ìä∏Î•º ÌÜµÌïú ÏãúÏä§ÌÖú ÏïàÏ†ïÏÑ± Í≤ÄÏ¶ù
          </p>
        </div>

        {/* Ï†úÏñ¥ Ìå®ÎÑê */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center space-x-2">
              <BarChart3 className="h-5 w-5" />
              <span>ÌÖåÏä§Ìä∏ Ï†úÏñ¥</span>
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex flex-wrap gap-4 items-center">
              <Button
                onClick={startTests}
                disabled={isRunning}
                className="bg-green-600 hover:bg-green-700"
              >
                <Play className="h-4 w-4 mr-2" />
                ÌÖåÏä§Ìä∏ ÏãúÏûë
              </Button>
              
              <Button
                onClick={pauseTests}
                disabled={!isRunning}
                variant="outline"
                className="border-yellow-300 text-yellow-700 hover:bg-yellow-50"
              >
                <Pause className="h-4 w-4 mr-2" />
                ÏùºÏãúÏ†ïÏßÄ
              </Button>
              
              <Button
                onClick={stopTests}
                disabled={!isRunning}
                variant="outline"
                className="border-red-300 text-red-700 hover:bg-red-50"
              >
                <Square className="h-4 w-4 mr-2" />
                Ï§ëÏßÄ
              </Button>
              
              <Button
                onClick={resetTests}
                variant="outline"
                className="border-gray-300 text-gray-700 hover:bg-gray-50"
              >
                <Database className="h-4 w-4 mr-2" />
                Ïû¨ÏÑ§Ï†ï
              </Button>
            </div>
            
            {/* ÏßÑÌñâÎ•† ÌëúÏãú */}
            {isRunning && (
              <div className="mt-4">
                <div className="flex justify-between text-sm text-gray-600 mb-2">
                  <span>Ï†ÑÏ≤¥ ÏßÑÌñâÎ•†</span>
                  <span>{Math.round(progress)}%</span>
                </div>
                <Progress value={progress} className="h-2" />
                {currentTest && (
                  <p className="text-sm text-blue-600 mt-2">
                    ÌòÑÏû¨ Ïã§Ìñâ Ï§ë: {currentTest}
                  </p>
                )}
              </div>
            )}
          </CardContent>
        </Card>

        {/* Ï†ÑÏ≤¥ ÌÜµÍ≥Ñ */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center space-x-2">
              <TrendingUp className="h-5 w-5" />
              <span>Ï†ÑÏ≤¥ ÌÜµÍ≥Ñ</span>
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
              <div className="text-center">
                <div className="text-2xl font-bold text-blue-600">
                  {overallStats.totalTests.toLocaleString()}
                </div>
                <div className="text-sm text-gray-600">Ï¥ù ÌÖåÏä§Ìä∏</div>
              </div>
              
              <div className="text-center">
                <div className="text-2xl font-bold text-green-600">
                  {overallStats.passedTests.toLocaleString()}
                </div>
                <div className="text-sm text-gray-600">ÏÑ±Í≥µ</div>
              </div>
              
              <div className="text-center">
                <div className="text-2xl font-bold text-red-600">
                  {overallStats.failedTests.toLocaleString()}
                </div>
                <div className="text-sm text-gray-600">Ïã§Ìå®</div>
              </div>
              
              <div className="text-center">
                <div className="text-2xl font-bold text-yellow-600">
                  {overallStats.errorTests.toLocaleString()}
                </div>
                <div className="text-sm text-gray-600">Ïò§Î•ò</div>
              </div>
            </div>
            
            {/* Ïã§Ìñâ ÏãúÍ∞Ñ */}
            {overallStats.startTime > 0 && (
              <div className="mt-4 text-center text-sm text-gray-600">
                {overallStats.endTime > 0 ? (
                  <span>
                    Ï¥ù Ïã§Ìñâ ÏãúÍ∞Ñ: {Math.round((overallStats.endTime - overallStats.startTime) / 1000)}Ï¥à
                  </span>
                ) : (
                  <span>
                    ÏãúÏûë ÏãúÍ∞Ñ: {new Date(overallStats.startTime).toLocaleTimeString()}
                  </span>
                )}
              </div>
            )}
          </CardContent>
        </Card>

        {/* Í∞úÎ≥Ñ ÌÖåÏä§Ìä∏ Í≤∞Í≥º */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {testResults.map((test, index) => (
            <Card key={index}>
              <CardHeader>
                <CardTitle className="flex items-center justify-between">
                  <div className="flex items-center space-x-2">
                    {getStatusIcon(test.status)}
                    <span>{test.testName}</span>
                  </div>
                  <Badge className={getStatusBadge(test.status)}>
                    {test.status === 'pending' && 'ÎåÄÍ∏∞'}
                    {test.status === 'running' && 'Ïã§ÌñâÏ§ë'}
                    {test.status === 'passed' && 'ÏÑ±Í≥µ'}
                    {test.status === 'failed' && 'Ïã§Ìå®'}
                    {test.status === 'error' && 'Ïò§Î•ò'}
                  </Badge>
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {/* ÏßÑÌñâÎ•† */}
                  <div>
                    <div className="flex justify-between text-sm text-gray-600 mb-1">
                      <span>ÏßÑÌñâÎ•†</span>
                      <span>
                        {test.totalTests > 0 
                          ? Math.round(((test.passedTests + test.failedTests + test.errorTests) / test.totalTests) * 100)
                          : 0
                        }%
                      </span>
                    </div>
                    <Progress 
                      value={test.totalTests > 0 
                        ? ((test.passedTests + test.failedTests + test.errorTests) / test.totalTests) * 100
                        : 0
                      } 
                      className="h-2" 
                    />
                  </div>
                  
                  {/* ÏÉÅÏÑ∏ ÌÜµÍ≥Ñ */}
                  <div className="grid grid-cols-3 gap-2 text-sm">
                    <div className="text-center">
                      <div className="font-semibold text-green-600">{test.passedTests.toLocaleString()}</div>
                      <div className="text-gray-500">ÏÑ±Í≥µ</div>
                    </div>
                    <div className="text-center">
                      <div className="font-semibold text-red-600">{test.failedTests.toLocaleString()}</div>
                      <div className="text-gray-500">Ïã§Ìå®</div>
                    </div>
                    <div className="text-center">
                      <div className="font-semibold text-yellow-600">{test.errorTests.toLocaleString()}</div>
                      <div className="text-gray-500">Ïò§Î•ò</div>
                    </div>
                  </div>
                  
                  {/* Ïã§Ìñâ ÏãúÍ∞Ñ */}
                  {test.duration && (
                    <div className="text-sm text-gray-600 text-center">
                      Ïã§Ìñâ ÏãúÍ∞Ñ: {Math.round(test.duration / 1000)}Ï¥à
                    </div>
                  )}
                  
                  {/* Ïò§Î•ò ÏÉÅÏÑ∏ */}
                  {test.details.length > 0 && (
                    <div className="mt-3">
                      <details className="text-sm">
                        <summary className="cursor-pointer text-red-600 font-medium">
                          Ïò§Î•ò ÏÉÅÏÑ∏ Î≥¥Í∏∞ ({test.details.length}Í∞ú)
                        </summary>
                        <div className="mt-2 space-y-1 max-h-32 overflow-y-auto">
                          {test.details.slice(-10).map((detail, detailIndex) => (
                            <div key={detailIndex} className="text-red-600 bg-red-50 p-2 rounded text-xs">
                              {detail}
                            </div>
                          ))}
                        </div>
                      </details>
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    </div>
  )
}
